body {
  letter-spacing: .03em;
  color: #2c2c2c;
  font-family: Roboto, Kalam, sans-serif;
}

img {
  width: 100%;
  object-fit: cover;
  object-position: center;
  display: block;
}

h1, h2, h3, h4, h5, h6, ul, p {
  margin-top: 0;
  margin-bottom: 0;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

